import React, { Component } from 'react'
import Range from 'rc-slider/lib/Range';
export default class RangeSliderFilter extends Component {
    render() {
        const { value, min, max, onAfterChange } = this.props;        
        return (
            <Range
                value={value}
                min={min}
                max={max}
                onChange={onAfterChange}
            />

        )
    }
}
